<template>
  <!-- <vue-chart :chartType="'PieChart'" :columns="getColumns(block)" :rows="getRows(block)" :options="options"></vue-chart> -->
  <block-graph :datas="display"></block-graph>
</template>

<script>
const BlockGraph = () =>
  import("@/components/Shared/Charts/BlockGraph.component.vue");
export default {
  props: {
    block: {
      type: Object,
      required: true
    },
    lang: {
      type: String,
      required: true
    },
    datas: {
      type: Object,
      required: true
    }
  },
  methods: {},
  data() {
    return {
      display: [20,58,98,12]
    };
  },
  components: {
    BlockGraph
  }
};
</script>

<style>

</style>



